.home{
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

}
.intro{
    max-width: 540px;
    text-align: center;
    z-index: 1;
}


.home__img{
    margin-bottom: 1.5rem;
    border-radius: 50%;
    width: 40%;
}
.home__name{
    font-size: var(--h1-font-size);
    font-weight: var(--font-bold);
    margin-bottom: 0.5rem;
} 

.scroll__down{
    position:absolute;
    bottom: 2.5rem;
    left: 0;
    width: 100%;

}
.btn__intro{
    margin-top: 1rem;
    display: flex;
    justify-content: center;
}
.btn__intro a:first-child{
    margin-right: 10px;
}
.home__scroll--name{
    font-size: var(--small-font-size);
}
.mouse{
    border: 2px solid #454360;
    display: block;
    height: 1.6rem;
    width: 1.25rem;
    margin: auto;
    margin-top: .75rem;
    border-radius: 1rem;
    position: relative;
}
@keyframes ani-mouse {
    0%{
        top: 29%;
    }
    15%{
        top: 50%;
    }
    50%{
        top: 50%;
    }
    100%{
        top: 29%;
    }
}
.wheel{
    background-color: var(--title-color);
    border-radius: 100%;
    width: 0.25rem;
    height: 0.25rem;
    position: absolute;
    top: 0.5rem;
    left: 50%;
    transform: translateX(-50%);
    animation: ani-mouse 2s linear infinite;
}
.shapes{
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
}
.shape{
    position: absolute;

}
.s1{
    left: 2%;
    top: 10%;

}
.s2{
    left: 18%;
    top: 30%;
}
.s3{
    left: 5%;
    bottom: 30%;
}
